












































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Header from '@/components/layout/Header.vue';
import InfoSide from '@/components/InfoSide.vue';
import Stepper from '@/components/utilities/Stepper.vue';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { required, requiredIf } from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import { User } from '@/modules/auth/interfaces/user.interface';
import { USER_EMPTY_STATE } from '@/modules/auth/emptyStates/user.emptyState';
import { auth , countries} from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import CountryMethods from '@/store/countries/methods/countries.methods';
import { NationalityCountry } from '@/modules/countries/interfaces/countries.interface';
import InputFile from '@/components/utilities/InputFile.vue';
import { CountryDocType } from '@/modules/countries/interfaces/countries.interface';
import {availableImagesTypes} from '@/utils/constants'
import { getSpanishLanCountries, showToast } from '@/utils/global-functions';
import {_arrayBufferToBase64} from '@/utils/image-functions'
import store from '@/store';

@Component({
    components: {
        Header,
        InfoSide,
        Stepper,
        SvgIcon,
        InputFile
    },
    mixins: [validationMixin],
    validations:{
        levelOneRequirements: {
            ident_doc_number:{
                value: {required}
            },
            id_ident_doc_type:{
                value: {required: requiredIf(function(){
                    return this.isFirstQuestion 
                }) }
            },
            doc:{
                value: {required}
            }
        },
        nationalityDocType:{
            country_name:{required: requiredIf(function(){
                return !this.isFirstQuestion
            })}
        }
    }
})
export default class SecondStep extends Vue {
   @Prop() levelOneRequirements!: any;
    docTypes : CountryDocType[] = []
    formats: Array<string> = []
    user: User = USER_EMPTY_STATE;
    docType : CountryDocType = {
        id_ident_doc_type : '',
        name_doc_type: '',
        id_resid_country: -1,
        type_doc_type : '',
        id_ip_country: -1,
        name_country: '',
        acronym: ''
    }
    nationalityDocType : Partial<NationalityCountry> ={country_name : ''};
    questions = [
      {
          question: `Posee usted &nbsp;<span class="fs-small fw-bold">alguno</span>&nbsp; de los siguientes documentos `,
          answer: null,
          show: true,
          open: 1,
          docs: []
      },
      {
          question: 'Posee usted Pasaporte?',
          answer: null,
          show: false,
          open: 2
      },
      {
          question: 'Posee usted Cdula/Documento de Identidad de acuerdo a su nacionalidad?',
          answer: null,
          show: false
      }
    ]
    nationalityCountriesSpanish = []
    loader = true;
    imageAlreadyExists = false

    async mounted(){
        if (this.levelOneRequirements.doc.rejected === false)
            this.imageAlreadyExists = true;
        this.formats = availableImagesTypes;  
        this.user =  this.userData; 
        this.questions[0].question = this.questions[0].question + `&nbsp;<span class="fs-small fw-bold">(${this.user.name_resid_country})</span>`
        await this.fetchDocTypes();
        this.getMainDocs();
        this.showMainDocsString();
        await this.fetchNationalityCountries();
        this.nationalityCountriesSpanish = getSpanishLanCountries(this.nationalityCountries)
        this.setPreviousDocTypes();
        let userNationalityId = this.user.id_nationality_country;
        if (typeof userNationalityId === 'string') userNationalityId = parseInt(userNationalityId);  
        this.loader = false;
    }

    setPreviousDocTypes(){
        if (this.levelOneRequirements.id_ident_doc_type.rejected === false || this.userDocType !== 0){
          let docType = this.mainDocTypes.find(el => el.id_ident_doc_type === this.levelOneRequirements.id_ident_doc_type.value)
          let secondaryDocType = this.secondaryDocTypes.find(el => el.id_ident_doc_type === this.levelOneRequirements.id_ident_doc_type.value)
          if (docType !== undefined){ 
              if (docType.id_resid_country === this.user.id_resid_country){
                this.docType = docType;
                this.questions[0].answer = true;
                this.questions[0].show = true;
              }
              else {
                  this.nationalityDocType = this.nationalityCountries.find(el => el.country_name === docType.name_country)
                  this.questions[0].answer = false;
                  this.questions[1].answer = false;
                  this.questions[2].answer = true;
                  this.questions[2].show = true;
              }
          }
          if (secondaryDocType !== undefined){ 
              this.nationalityDocType = this.nationalityCountries.find(el => el.country_name === secondaryDocType.name_country)
              this.questions[0].answer = false;
              this.questions[1].answer = true;
              this.questions[1].show = true;
          }
        }
    }

    get isFirstQuestion(){
        return this.questions[0].answer === true
    }

    /* Add main docs to first question (doc array).  */
    showMainDocsString(){
        this.mainDocTypes.forEach(el =>{
           if ((el.type_doc_type === "Principal" || el.type_doc_type === "Terciario") && el.id_resid_country === this.user.id_resid_country)
            this.questions[0].docs.push(el.acronym+' - '+el.name_doc_type)
        })
        this.questions[0].docs[this.questions[0].docs.length-1] += ' ?'
    }

    /* Get the selected question number */
    get selectedDocType(){
        // store.state.auth.selectedTypeDocType = this.questions.findIndex(el => el.answer === true) + 1;
        return this.userDocType;
    }

    /* if first question = yes, then give me the main doc types for the dropdown  */
    getMainDocs(){
        //if (this.selectedDocType === 1) {
            this.docTypes = this.mainDocTypes.filter(el => el.id_resid_country === this.user.id_resid_country);
       // }
    }

    /* If some question = yes */
    get someYes(){
        let yes = false
        this.questions.forEach(el =>{
            if (el.answer === true)  yes = true
        })
        return yes
    }

    submit(){
        this.$v.$touch();
        if (!this.$v.$invalid){
            this.setSelectedQuestion()             
             if (!this.someYes){
                showToast('Debe seleccionar un tipo de documento','error')
            }
            else if (!this.$v.levelOneRequirements.doc.value.$model.name && !this.imageAlreadyExists) showToast('Debe anexar una imagen de su documento de identidad','error')
            else this.$emit('nextStep')  
        }
        if (this.$v.levelOneRequirements.doc.value.$invalid) showToast('Debe anexar una imagen de su documento de identidad','error')
    }

    goBack(){
        this.setSelectedQuestion()
        this.$emit('prevStep')
    }

    setSelectedQuestion(){
         let questionId = this.questions.findIndex(el => el.answer === true) + 1;
         this.saveSelectedDocQuestion(questionId)
          /* If user selects second or third question, save the state country Id  */
          if (this.userDocType === 2 || this.userDocType === 3 ){
            this.saveSelectedCountryIdDoc(this.nationalityDocType.id_all_country);
            if (this.nationalityDocType.id_all_country !== ""){
                if (this.userDocType === 2)
                    this.levelOneRequirements.id_ident_doc_type.value = this.secondaryDocTypes.find(el => el.name_country === this.nationalityDocType.country_name).id_ident_doc_type
                if (this.userDocType === 3)
                    this.levelOneRequirements.id_ident_doc_type.value = this.mainDocTypes.find(el => el.name_country === this.nationalityDocType.country_name).id_ident_doc_type
            }
          }
    }

    getImage(image: string){
        this.levelOneRequirements.doc.value = image;
    }

    hideQuestions(question: any){
        if (this.levelOneRequirements.id_ident_doc_type.rejected !== true &&
            this.levelOneRequirements.ident_doc_number.rejected !== true){
            question.answer = true
            this.nationalityDocType = {};
            this.$v.$reset();
            let start = false;
            this.questions.forEach(el =>{
                if (el === question) start = true;
                if (el !== question && start) {
                    el.show = false;
                    el.answer = null;
                }
            })
            this.getMainDocs();
        }
    }

    showQuestion(question: any){
        if (this.levelOneRequirements.id_ident_doc_type.rejected !== true &&
            this.levelOneRequirements.ident_doc_number.rejected !== true){
            question.answer = false
            let questionFound = this.questions.findIndex(el => el === question)
            if ((questionFound + 1) !== this.questions.length)
                this.questions[question.open].show = true
            this.getMainDocs();
        }
    }

    getImageSrc(imageContent: any){
        if (imageContent.data)
            return _arrayBufferToBase64(imageContent.data);
        return _arrayBufferToBase64(imageContent);
    }



    @auth.Getter(AuthMethods.getters.GET_USER_DATA)
    userData!: User;
    @auth.Action(AuthMethods.actions.SAVE_SELECTED_COUNTRY_ID_DOC)
    saveSelectedCountryIdDoc!: (countryId: string) => void;
    @auth.Action(AuthMethods.actions.SAVE_SELECTED_DOC_QUESTION_NUMBER)
    saveSelectedDocQuestion!: (docQuestionId: number) => void;
    @countries.Action(CountryMethods.actions.FETCH_DOC_TYPES)
    fetchDocTypes!: () => Promise<boolean>;
    @countries.Getter(CountryMethods.getters.GET_MAIN_DOC_TYPES)
    mainDocTypes!: CountryDocType[];
    @countries.Getter(CountryMethods.getters.GET_SECONDARY_DOC_TYPES)
    secondaryDocTypes!: CountryDocType[];
    @auth.Getter(AuthMethods.getters.GET_SELECTED_DOC_TYPE)
    userDocType!: number;
    @countries.Action(CountryMethods.actions.FETCH_ALL_NATIONALITY_COUNTRIES)
    fetchNationalityCountries!: () => Promise<boolean>
    @countries.Getter(CountryMethods.getters.GET_ALL_NATIONALITY_COUNTRIES)
    nationalityCountries!: NationalityCountry[]
}
