















































































































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Header from '@/components/layout/Header.vue';
import InfoSide from '@/components/InfoSide.vue';
import Stepper from '@/components/utilities/Stepper.vue';
import { required, requiredIf } from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import { User } from '@/modules/auth/interfaces/user.interface';
import { USER_EMPTY_STATE } from '@/modules/auth/emptyStates/user.emptyState';
import { auth, countries } from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import { formatDate , isoDateToDDMMYYYY, validFormatDate, validYearDate } from '@/utils/date-functions';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import {getStates} from 'country-state-picker';
import CountryMethods from '@/store/countries/methods/countries.methods';
import { CountryDocType } from '@/modules/countries/interfaces/countries.interface';
import Toast from '@/components/utilities/Toast.vue';



@Component({
    components: {
        Header,
        InfoSide,
        Stepper,
        SvgIcon,
        Toast
    },
    mixins: [validationMixin],
    validations:{
        levelOneRequirements:{
            date_birth:{
                value: {required,validFormatDate, validYearDate}
            },
            resid_city:{
                value: {required}
            },
            state_name:{
                value: {required}
            },
            pol_exp_per: {
                value: {required: requiredIf(function(){
                    return this.politicallyExponseQuestion 
                })}
            },
            occupation: {
                value: {required}
            },
            main_sn_platf:{
                value: {}
            },
            user_main_sn_platf:{
                value: {required: requiredIf(function(){
                    return this.levelOneRequirements.main_sn_platf.value !== null && this.levelOneRequirements.main_sn_platf.value !== 'Ninguna'
                })}
            }
        }
    }
})
export default class FirstStep extends Vue {
  @Prop() levelOneRequirements!: any;
  user: User = USER_EMPTY_STATE;
  states : Array<string> = [];
  date = '';
  loader = true;
  politicallyExponseQuestion = false;
  platforms = ['Ninguna','Twitter','Instagram','Facebook']
  

    async mounted(){
        this.user =  this.userData;
        
        this.removeAt();
        this.getIsoCode();
        if (this.levelOneRequirements.main_sn_platf.value.length === 0 || this.levelOneRequirements.main_sn_platf.value === null)
         this.levelOneRequirements.main_sn_platf.value = this.platforms[0]
        // let residCountry = this.levelOneRequirements.id_resid_country.value;
        let residCountry = this.user.id_resid_country
        if (typeof residCountry === 'string') residCountry = parseInt(residCountry);
        this.politicallyExponseQuestion = await this.politicallyExponse(residCountry);
        this.loader = false;
    }

    @Watch('levelOneRequirements.email_user.value')
    watchLevelOneRequirements(){
        this.removeAt();
    }

    removeAt(){
        if (this.levelOneRequirements.user_main_sn_platf.value.length && this.levelOneRequirements.user_main_sn_platf.value[0] === '@'){ 
         this.levelOneRequirements.user_main_sn_platf.value = this.levelOneRequirements.user_main_sn_platf.value.substring(1)}
    }

    async getIsoCode(){
        let isoCode : string | boolean = await this.getCountryCode(this.user.id_resid_country);
        if (typeof isoCode !== 'boolean'){
            this.states = getStates(isoCode);
        }    
    }

    @Watch('date')
        changeDateFormat(val: string){
           this.levelOneRequirements.date_birth.value = formatDate(val);
        }
    
    get validateUsername(){
        if (this.levelOneRequirements.main_sn_platf.value !== 'Facebook' && this.levelOneRequirements.user_main_sn_platf.value[0] !=='@'){
            return '@'+this.levelOneRequirements.user_main_sn_platf.value
        }
        return this.levelOneRequirements.user_main_sn_platf.value
    }

    submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
                this.levelOneRequirements.user_main_sn_platf.value = this.validateUsername;
                this.$emit('nextStep');            
        }
    }

    @auth.Getter(AuthMethods.getters.GET_USER_DATA)
    userData!: User;
    @countries.Getter(CountryMethods.getters.GET_MAIN_DOC_TYPES)
    mainDocTyoes!: CountryDocType[];
    @countries.Getter(CountryMethods.getters.GET_SECONDARY_DOC_TYPES)
    secondaryDocTyoes!: CountryDocType[];
    @countries.Action(CountryMethods.actions.GET_ISO_CODE)
    getCountryCode!: (countryId: number | string) => Promise<string | boolean>
    @countries.Action(CountryMethods.actions.POLITICALLY_EXPOSE_QUESTION)
    politicallyExponse!: (countryId : number) => Promise<boolean>
}
