



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import SvgIcon from '@/components/utilities/SvgIcon.vue'
import InputFile from '@/components/utilities/InputFile.vue';
import { User } from '@/modules/auth/interfaces/user.interface';
import { USER_EMPTY_STATE } from '@/modules/auth/emptyStates/user.emptyState';
import { auth} from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import { required} from 'vuelidate/lib/validators';
import { validationMixin } from 'vuelidate';
import Loader from '@/components/utilities/Loader.vue';
import {availableImagesTypes} from '@/utils/constants'
import store from '@/store';
import { showToast } from '@/utils/global-functions';
import { _arrayBufferToBase64 } from '@/utils/image-functions';
@Component({
    components:{
        InputFile,
        Loader,
        SvgIcon
    },
    mixins: [validationMixin],
    validations:{
        levelOneRequirements: {
            selfie: {
                value: {required}
            }
        }
    }
})
export default class ThirdStep extends Vue {
    @Prop() levelOneRequirements!: any;
    user: User = USER_EMPTY_STATE;
    $refs : any ={}
    formats = []
    imageAlreadyExists = false;

    created(){
        if (this.levelOneRequirements.selfie.rejected === false)
            this.imageAlreadyExists = true;
        this.user =  this.userData;  
        this.formats = availableImagesTypes; 
    }
    async submit(){
        this.$v.$touch();
        if (!this.$v.$invalid){
            if (!this.$v.levelOneRequirements.selfie.value.$model.name && !this.imageAlreadyExists) this.showImageError();
            else{
                this.$refs.loader.showLoader();
                let data = {question: this.selectedDocType, country: this.selectedCountryIdDoc, data: this.levelOneRequirements}
                if (await this.verifyLevelOne(data)){
                    this.$router.push({name: 'LevelSuccess'})
                }
                else{
                    showToast('Error verificando la cuenta','error')
                }
                this.$refs.loader.hideLoader();
            }        
        }  
        else {
             this.showImageError();
        }
    }

    showImageError(){
        showToast('Debe anexar una imagen de su documento de identidad','error')
    }

    getImageSrc(imageContent: any){
        if (imageContent.data)
            return _arrayBufferToBase64(imageContent.data);
        return _arrayBufferToBase64(imageContent);
    }

    goBack(){
        this.$emit('prevStep')
    }

    getImage(image: string){
        this.levelOneRequirements.selfie.value = image
    }

    @auth.Getter(AuthMethods.getters.GET_USER_DATA)
    userData!: User;
    @auth.Action(AuthMethods.actions.VERIFY_LEVEL_ONE)
    verifyLevelOne!: (data: {question: number, country: string, data: any}) => Promise<boolean>;
    @auth.Getter(AuthMethods.getters.GET_SELECTED_DOC_TYPE)
    selectedDocType!: number;
    @auth.Getter(AuthMethods.getters.GET_SELECTED_COUNTRY_DOC)
    selectedCountryIdDoc!: string;
}
